export function main() {
	const btn = document.querySelector('button');
	const input = document.querySelector('input');

	const cityDiv = document.getElementById('city');
	const temperatureDiv = document.getElementById('temperature');
	const conditionDiv = document.getElementById('condition');
	const feelsLikeDiv = document.getElementById('feelsLike');
	const humidityDiv = document.getElementById('humidity');
	const windDiv = document.getElementById('wind');

	async function weather(iv) {
		if (iv === '' || iv === undefined) iv = 'Skopje';

		let resJson;
		try {
			let response = await fetch(
				`http://api.weatherapi.com/v1/current.json?key=1d84d36f98094b9ea12181117230505&q=${iv}&aqi=no`,
				{ mode: 'cors' }
			);
			resJson = await response.json();
		} catch (err) {
			console.log(err);
		}

		cityDiv.innerHTML = `<i class="fa-solid fa-location-dot"></i> ${resJson.location.name} `;
		conditionDiv.innerHTML = `<i class="fa-solid fa-fan"></i> ${resJson.current.condition.text}`;
		temperatureDiv.innerHTML = `<i class="fa-solid fa-temperature-three-quarters"></i> ${resJson.current.temp_c}C°`;
		feelsLikeDiv.innerHTML = `<i class="fa-solid fa-temperature-three-quarters"></i> ${resJson.current.feelslike_c}C°`;
		humidityDiv.innerHTML = `<i class="fa-solid fa-droplet"></i> ${resJson.current.humidity}%`;
		windDiv.innerHTML = `<i class="fa-solid fa-wind"></i> ${resJson.current.wind_kph}<span class='kph'>kph</span>`;

		console.log(
			`Humidity: ${resJson.current.humidity}%,
			 Wind: ${resJson.current.wind_kph}kph,
			 Condition: ${resJson.current.condition.text}
			  `
		);
	}

	weather('Skopje');
	btn.addEventListener('click', () => weather(input.value));
	input.addEventListener('keydown', (e) => {
		if (e.keyCode === 13) {
			weather(input.value);
		}
	});
}
